.container {
    text-align: center;
    background-color: #1e1e1e;
    color: white;
    min-height: 100vh;
    padding: 20px;
  }
  
  .title {
    font-size: 24px;
    margin-bottom: 10px;
  }
  
  .dropdown {
    padding: 10px;
    margin-bottom: 10px;
    background-color: #333;
    color: white;
    border: none;
  }
  
  .input-box {
    width: 80%;
    height: 50px;
    padding: 10px;
    margin-top: 10px;
    background-color: #333;
    color: white;
    border: none;
  }
  
  .run-button {
    padding: 10px 20px;
    background-color: #007bff;
    border: none;
    color: white;
    margin-top: 10px;
    cursor: pointer;
  }
  
  .run-button:hover {
    background-color: #0056b3;
  }
  
  .output-box {
    width: 80%;
    background-color: #222;
    padding: 15px;
    margin-top: 10px;
    text-align: left;
    border-radius: 5px;
  }
  