body {
  font-family: "Poppins", sans-serif;
  background-color: #121212;
  color: #ffffff;
  margin: 0;
  padding: 0;
}

.compiler-container {
  text-align: center;
  max-width: 800px;
  margin: auto;
  padding: 20px;
}

.title {
  font-size: 28px;
  font-weight: bold;
  margin-bottom: 15px;
  color: #00c3ff;
}

.controls {
  display: flex;
  justify-content: space-between;
  margin-bottom: 10px;
}

.dropdown {
  padding: 10px;
  background-color: #333;
  color: white;
  border: 1px solid #555;
  border-radius: 5px;
  outline: none;
  cursor: pointer;
}

.run-button {
  padding: 10px 20px;
  background-color: #00c3ff;
  border: none;
  color: black;
  font-weight: bold;
  border-radius: 5px;
  cursor: pointer;
  transition: 0.3s;
}

.run-button:hover {
  background-color: #008fcc;
}

.editor-container {
  margin: 10px 0;
  border: 2px solid #00c3ff;
  border-radius: 5px;
  overflow: hidden;
}

.input-box {
  width: 100%;
  height: 50px;
  padding: 10px;
  background-color: #222;
  color: white;
  border: 1px solid #555;
  margin-top: 10px;
  border-radius: 5px;
  outline: none;
}

.output-box {
  width: 100%;
  background-color: #222;
  padding: 15px;
  margin-top: 15px;
  text-align: left;
  border-radius: 5px;
  border: 1px solid #00c3ff;
}

pre {
  font-size: 16px;
  white-space: pre-wrap;
  word-wrap: break-word;
}
